/* settings api
----------------------------------------------------------------------------- */
@media all {
    .uploaded-file-url,
    .uploaded-image {
        display: none;
        padding: 0.5rem;
    }

    .settings-type-radio {
        list-style-type: none;
    }

    .settings-type-radio li.horizontal {
        display: inline-block;
        margin: 0.5rem;
        max-width: 100px;
        text-align: center;
        vertical-align: top;
    }

    .settings-type-radio li.horizontal input {
        margin-right: 0;
        width: 100%;
    }

    .settings-type-radio figure {
        margin: 0.5rem 0 0;
    }

    .upload-button a {
        text-decoration: none;
    }

    .remove {
        margin-top: 0.5rem;
    }

    ul.settings-group {
        margin-top: 0;
    }

    .image img {
        border: 1px solid rgb(221 221 221);
        vertical-align: bottom;
    }

    .image img:hover {
        cursor: pointer;
    }

    .nav-tab:focus {
        box-shadow: none;
        outline: none;
    }

    .rsa-delete {
        color: rgb(170 0 0);
    }

    .rsa-delete:hover {
        color: rgb(255 0 0);
    }
}
